				}
			],

			history_item: [
				{
					"type":"date",
					"text":"2014~2015",
					"sub":[
						{
							"type":"text",
							"text":"開始接觸Linux"
						},
						{
							"type":"text",
							"text":"在家裡架設了檔案伺服器＆VPN"
						},
